	module.exports = {"data":{"post":{"html":"<p>The flood waters from Dresden are heading north. Flooding is expected in the outskirts upstream of Hamburg by Thursday or Friday. However, Hamburg itself will be barely affected, since the harbor is large enough to disapate the flood. Our county (Landkreis Harburg) has declared an emergency for the Elbmarsch, where 12000 residents may have to be evacuated. The PapaScott residence is 50 km away from the area that might be flooded, so we are in no danger.</p>","fields":{"title":"Flood Water Coming","date":"19 Aug 2002","url":"https://papascott.de/2002-08-19-flood-water-coming/"}}},"pathContext":{"slug":"/2002-08-19-flood-water-coming/","prev":{"fields":{"title":"Vacation Weekend","date":"18 Aug 2002","slug":"/2002-08-18-vacation-weekend/"}},"next":{"fields":{"title":"Americans Stay Home","date":"20 Aug 2002","slug":"/2002-08-20-americans-stay-home/"}}}}

